  position: absolute;
  left: 0px;
  top: 0;
  height: 15px;
  line-height: 15px;
  max-width: 0;
  font-size: 14px;
  color: #ffffff;
  font-weight: 800;
  overflow: hidden;
  background: transparent;
  text-align: center;
  font-family: 'FontAwesome';
  transition: max-width 500ms ease;
}
.styled-form .form-group .check-box input[type="checkbox"]:checked + label {
